export default {
	data() {
		return { invCount: 0 };
	},
	methods: {
		handleContinue() {
			uni.redirectTo({
				url: '/pages/ticket/choose-image/choose-image?biz=dksp'
			});
		},
		handleGoHome() {
			uni.removeStorageSync('scanGrant');
			uni.switchTab({
				url: '/pages/tab-bar/home/home'
			});
		}
	},
	onLoad(option) {
		this.invCount = option.invCount;
	}
};
